<template>
  <div>
    <v-breadcrumbs
      :items="items"
      divider="/"
    ></v-breadcrumbs>
    <v-data-table
      :search="search"
      :headers="headers"
      :items="onemilestones"
      item-key="full_name"
      class="table-rounded"
      :loading="loadingPages"
    >
      <template v-slot:top>
        <v-toolbar
          flat
        >
          <v-toolbar-title>Capstone 1 Milestone List</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-dialog
            v-model="dialog"
            max-width="500px"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                color="primary"
                dark
                class="mb-2"
                v-bind="attrs"
                v-on="on"
                @click="openModal()"
              >
                New
              </v-btn>
            </template>
            <v-card>
              <v-toolbar
                dark
                color="primary"
              >
                <v-toolbar-title v-if="!isupdate">
                  Create
                </v-toolbar-title>
                <v-toolbar-title v-if="isupdate">
                  Update
                </v-toolbar-title>
                <v-spacer></v-spacer>
              </v-toolbar>

              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col
                      cols="12"
                      sm="12"
                      md="12"
                    >
                      <v-text-field
                        v-model="data.milestone"
                        label="Milestone Title"
                      ></v-text-field>
                    </v-col>
                    <v-col
                      cols="12"
                      sm="12"
                      md="12"
                    >
                      <v-select
                        v-model="data.type"
                        :items="milestonetype"
                        item-value="id"
                        required
                      ></v-select>
                    </v-col>
                    <v-col
                      cols="12"
                      sm="12"
                      md="12"
                    >
                      <v-text-field
                        v-model="data.percent"
                        label="Milestone Percent"
                      ></v-text-field>
                    </v-col>
                    <v-col
                      cols="12"
                      sm="12"
                      md="12"
                    >
                      <v-text-field
                        v-model="data.ordernumber"
                        label="Milestone Order Number"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="closeModal()"
                >
                  Cancel
                </v-btn>
                <v-btn
                  v-if="!isupdate"
                  color="primary"
                  :loading="loading"
                  :disabled="loading"
                  @click="create()"
                >
                  Save
                </v-btn>
                <v-btn
                  v-if="isupdate"
                  color="primary"
                  :loading="loading"
                  :disabled="loading"
                  @click="update()"
                >
                  Update
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
        <v-text-field
          v-model="search"
          label="Search..."
          class="mx-4"
        ></v-text-field>
      </template>
      <template #[`item.milestonetype`]="{item}">
        {{ item.milestone_type === 1 ? 'Document Type' : 'Form Type' }}
      </template>
      <template #[`item.actions`]="{item}">
        <v-icon
          small
          @click="selectUpdate(item)"
        >
          {{ icons.mdiBookEdit }}
        </v-icon>
      </template>
    </v-data-table>
  </div>
</template>

<script>
import sweetalert2 from '@/plugins/sweetalert2'
import { mdiSquareEditOutline, mdiDotsVertical, mdiBookEdit } from '@mdi/js'
import {
  mapGetters,
  mapActions,
} from 'vuex'

export default {
  setup() {
    return {
      isupdate: false,
      dialog: false,
      loadingPages: true,
      loading: false,
      formTitle: 'Create',
      items: [
        {
          text: 'Settings',
          disabled: true,
          href: '#',
        },
        {
          text: 'Milestones',
          disabled: true,
          href: '#',
        },
        {
          text: 'Capstone 1 Milestone',
          disabled: false,
          href: '#',
        },
      ],
      search: '',
      headers: [
        { text: 'CODE', value: 'code' },
        { text: 'MILESTONE', value: 'milestone' },
        { text: 'MILESTONE TYPE', value: 'milestonetype' },
        { text: 'PERCENT', value: 'percent' },
        { text: 'ODER NUMBER', value: 'ordernumber' },
        { text: 'ACTIONS', value: 'actions', sortable: false },
      ],

      // icons
      icons: {
        mdiSquareEditOutline,
        mdiDotsVertical,
        mdiBookEdit,
      },

      // Milestone type Data
      milestonetype: [
        { id: 1, text: 'Document Type' },
        { id: 2, text: 'Form Type' },
      ],

      // Input field
      data: {
        id: null,
        milestone: '',
        milestone_type: '',
        percent: '',
        ordernumber: '',
      },
    }
  },
  computed: {
    ...mapGetters(['onemilestones']),
  },
  mounted() {
    this.getOnemilestones().then(() => {
      this.loadingPages = false
    })
  },
  methods: {
    // eslint-disable-next-line no-unused-vars
    openModal() {
      this.isupdate = false
      this.data = {
        id: null,
        milestone: '',
        type: '',
        percent: '',
        ordernumber: '',
      }
    },
    create() {
      this.loading = true
      this.createOnemilestones(this.data).then(res => {
        this.loading = false
        if (res.data.status === 200) {
          sweetalert2('success', res.data.message)
          this.closeModal()
        } else {
          sweetalert2('warning', res.data.message)
        }
      }).catch(() => {
        sweetalert2('warning', 'Server error!')
      })
    },
    update() {
      this.loading = true
      this.updateOnemilestones(this.data).then(res => {
        this.loading = false
        if (res.data.status === 200) {
          sweetalert2('success', res.data.message)
          this.closeModal()
        } else {
          sweetalert2('warning', res.data.message)
        }
      }).catch(() => {
        sweetalert2('warning', 'Server error!')
      })
    },
    selectUpdate(item) {
      this.data = {
        id: item.id,
        milestone: item.milestone,
        type: item.milestone_type,
        percent: item.percent,
        ordernumber: item.ordernumber,
      }
      this.dialog = true
      this.isupdate = true
    },
    closeModal() {
      this.dialog = false
      this.isupdate = false
      this.data = {
        id: null,
        milestone: '',
        type: '',
        percent: '',
        ordernumber: '',
      }
    },
    ...mapActions(['getOnemilestones', 'createOnemilestones', 'updateOnemilestones']),
  },
}
</script>
